.dicbox {
  box-sizing: border-box;
  padding: 0;
  background-color: rgb(162, 248, 248);
  height: 100vh;
}
.inputcontainer {
  /* background-color: aqua; */
  height: 150px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputbox {
  /* background-color: azure; */
  width: 500px;
  text-align: center;
  padding: 10px;
  border-radius: 5px;
}
.titlename {
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 50px;
}
.inputbox input {
  margin-right: 10px;
  padding: 5px;
  width: 300px;
  border-radius: 5px;
  border: 1px solid gray;
}

.card-title {
  background-color: aquamarine !important;
  width: fit-content;
  padding: 8px;
  border-radius: 5px;
}
.card {
  font-size: 20px;
}
.auth {
  font-size: 15px !important;
}
.btn-outline-success {
  border-radius: 5px;
  margin-top: 0;
}
.def {
  font-size: 25px;
  margin-top: 12px;
}
.search {
  color: blue;
  text-transform: capitalize;
}
.example {
  color: brown;
}
@media screen and (max-width: 768px) {
  .btn-outline-success {
    margin-top: 10px;
  }
}
