.container {
  max-width: 600px;
  margin: 0;
  padding: 20px;
  text-align: center;
  font-family: Arial, sans-serif;
}

.data-container {
  border: 1px solid #ccc;
  padding: 10px;
  margin-top: 0px;
}

.data-container h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

pre {
  overflow-x: auto;
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 5px;
}
