/* Base styles */
.input-container {
  margin-top: 10px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center; /* Center on larger screens */
  flex-wrap: wrap;
}
.translation-container {
  box-sizing: border-box;
  padding: 0;
  background-color: rgb(42, 237, 175);
  height: 100vh;
}
.tranlationtext {
  display: flex;
  justify-content: center;
  /* margin-top: 20px; */
  font-size: 50px;
  margin-bottom: 20px;
}
.rightselect {
  margin-right: 20px !important;
}
.input-container input {
  padding: 10px;
  width: 100%; /* Fill the width of the container */
  max-width: 500px; /* Limit maximum width */
  margin-bottom: 10px;
  margin-top: 10px; /* Add space between input and other elements */
}

.input-container select {
  margin: 10px 5px; /* Adjust margins */
}

