.translation-box {
  margin-left: 20px;
}

.translation-box h5 {
  color: blueviolet;
}
.btn-success {
  margin-top: 9px;
  height: 48px;
}
.translation-box p {
  font-size: 35px;
  margin-top: 20px;
}
.fa-volume-high {
  cursor: pointer;
  font-size: 25px;
}
.fa-arrow-right {
  margin: 25px 7px;
}

/* Media query for responsiveness */
@media screen and (max-width: 768px) {
  .input-container {
    justify-content: flex-start; /* Align left on smaller screens */
    padding: 0 15px; /* Add padding to the sides */
  }
  .tranlationtext {
    font-size: 20px;
  }

  .input-container input {
    width: calc(100% - 20px); /* Adjust width */
    margin: 5px; /* Adjust margins */
  }

  .input-container select {
    margin: 5px;
  }
}
